.upload-wrapper {
  background-color: rgb(248, 248, 248);
  margin: 0px auto;
  width: 100%;
  height: 100%;
  padding-top: 16px;
  padding-bottom: 16px;
  display: flex;
  min-height: 100vh;
  margin-top: 60px;
  justify-content: space-between;

  .upload-container {
    width: 1100px;
    box-shadow: rgb(0 0 0 / 6%) 0px 2px 8px;
    border-radius: 8px;
    background-color: rgb(255, 255, 255);
    padding: 24px 56px;
    margin: 0px auto;

    .upload-title {
      font-family: Arial, Helvetica, sans-serif;
      font-size: 24px;
      line-height: 1.5;
      font-weight: 700;
    }

    .upload-sub-title {
      margin-top: 2px;

      .span {
        font-size: 18px;
        line-height: 1.5;
        font-weight: 400;
        color: rgba(22, 24, 35, .5);
      }
    }

    .upload-content {
      display: flex;
      margin-top: 24px;
      margin-bottom: 130px;

      .upload-content-left {
        margin-top: 24px;

        .input {
          display: none;
        }

        .upload-state {
          display: flex;
          justify-content: center;
          align-items: center;
          flex-direction: column;
          cursor: pointer;
          transition: background 0.15s ease-in-out 0s;
          padding-left: 35px;
          padding-right: 35px;
          width: 260px;
          height: 458px;
          border: 2px dashed rgba(22, 24, 35, 0.2);
          border-radius: 8px;

          &:hover {
            background-color: rgba(22, 24, 35, 0.03);
            border: 2px dashed rgba(254, 44, 85, 1);
            ;
          }

          .upload-state-title {
            margin-top: 24px;
            font-size: 18px;
            line-height: 1.5;
            font-family: ProximaNova, Arial, Tahoma, PingFangSC, sans-serif;
            font-weight: 600;
            color: rgba(22, 24, 35, 1);
          }

          .upload-state-sub-title {
            font-size: 14px;
            line-height: 1.5;
            font-weight: 400;
            color: rgba(22, 24, 35, 0.75);
            margin-top: 4px;
            margin-bottom: 24px;
          }

          .upload-state-notice {
            font-size: 14px;
            line-height: 1.5;
            font-weight: 400;
            color: rgba(22, 24, 35, 0.5);
            text-align: center;
          }

          .upload-state-notice:not(:last-child) {
            margin-bottom: 8px;
          }

          .select-file {
            width: 100%;
            height: 41px;
            margin-top: 32px;
            border-radius: unset;

          }
        }
      }

      .preview {
        margin-left: -20px;
        margin-right: 8px;

        .input {
          display: none;
        }

        .preview-v2 {
          position: relative;
          background: rgb(255, 255, 255);
          line-height: 0;
          width: 284px;
          height: 556px;

          .video-preview {
            height: 486px;
            width: 260px;
            background-color: rgb(0, 0, 0);
            object-fit: contain;
            margin-left: 12px;
            margin-top: 12px;
            border-radius: 0.5px;
          }

          .phone-preview {
            background-image: url(../../../assets/image/Phone.png);
            background-size: cover;
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0px;
            left: 0px;
            z-index: 1;
          }
        }
      }

      .upload-content-right {
        margin-left: 24px;
        flex: 1 1 0%;

        .form-item:not(:last-child) {
          margin-bottom: 24px;

          .form-header {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .form-label {
              font-size: 16px;
              line-height: 1.5;
              font-weight: 600;
              color: rgba(22, 24, 35, 1);
              display: inline;
            }

            .form-count {
              font-weight: 400;
              font-size: 13px;
              line-height: 17px;
              color: rgba(22, 24, 35, 0.5);
              white-space: pre;
            }
          }

          .form-footer {
            margin-top: 4px;
            display: flex;

            .form-textarea {
              height: 60px;
              width: 100%;
              border: 1px solid rgba(22, 24, 35, 0.12);
              outline: none;
              padding: 8px 12px;
              border-radius: 4px;
              resize: none;
              font-size: 16px;
            }

            .form-input {
              height: 40px;
              width: 100%;
              border: 1px solid rgba(22, 24, 35, 0.12);
              outline: none;
              padding: 8px 12px;
              border-radius: 4px;
              resize: none;
            }

            .form-select {
              line-height: 1.5;
              font-weight: 400;
              color: rgba(22, 24, 35, 1);
              ;
              height: 40px;
              width: 40%;
              border: 1px solid rgba(22, 24, 35, 0.12);
              outline: none;
              padding: 8px 12px;
              border-radius: 4px;
              resize: none;
            }

            .form-checkbox {
              display: flex;
              flex-direction: row;
              align-items: center;

              .label {
                flex-grow: 1;
                padding-inline: 12px 0px;
                order: 1;
              }

              .input {
                position: relative;
                width: 16px;
                height: 16px;
                flex-shrink: 0;
                margin-block-start: 3px;
              }

              &:not(:last-child) {
                margin-right: 32px;
              }
            }
          }
        }

        .button-container {
          display: flex;
          flex-direction: row;
          margin-top: 24px;

          .button {
            padding: 10px 60px;
            height: 47px;

            span {
              text-align: center;
              margin: 0 auto;
            }
          }

          .discard {
            margin-right: 16px;
            background-color: transparent;
          }
        }
      }
    }
  }
}